/* @flow strict-local */
import React from "react";
import { navigate } from "@reach/router";
import { routes } from "../App";
import NavbarSuggestions from "./NavbarSuggestions";
import "../index.css";

const path = require("path");

class Navbar extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      errorMessage: "",
      index: 0,
      text: "",
      renderCaret: false
    };

    this.navRef = React.createRef();
  }

  _handleChange = event => {
    this.setState({ text: event.target.value });
  };

  _handleSelect = event => {
    this.setState({ index: event.target.selectionStart });
  };

  _toggleCaret = shouldRenderCaret => {
    this.navRef.current.focus();
    this.setState({ renderCaret: shouldRenderCaret });
  };

  updateStateText(text, index, letter) {
    const prefix = text.slice(0, index);
    const suffix = text.slice(index);

    if (letter === "Backspace") {
      return prefix.slice(0, -1) + suffix;
    }
    if (letter === "Delete") {
      return prefix + suffix.slice(1);
    }
    return prefix + letter + suffix;
  }

  renderCaret(index) {
    const spaces = " ".repeat(index);
    return (
      <div className="caret-display">
        <pre>{spaces}</pre>
        <pre className="navbar-caret"> </pre>
      </div>
    );
  }

  render() {
    const { errorMessage, text, index, renderCaret } = this.state;

    const renderedText = renderCaret ? this.renderCaret(index) : null;
    return (
      <div className="navbar">
        <div
          className="navbar-input-wrapper"
          tabIndex={1}
          onBlur={() => this._toggleCaret(false)}
          onFocus={() => this._toggleCaret(true)}
        >
          <pre className="navbar-path">{"~" + this.props.path}</pre>
          <pre> $ cd </pre>
          <input
            className="navbar-input"
            ref={this.navRef}
            type="text"
            value={text}
            onChange={this._handleChange}
            onSelect={this._handleSelect}
          />
          {renderedText}
        </div>
        {errorMessage !== "" && (
          <div className="navbar-error-message">
            <pre>{errorMessage}</pre>
          </div>
        )}
        <NavbarSuggestions path={this.props.path} />
      </div>
    );
  }
}

export default Navbar;
